.body {
  padding: 0px;
  margin: 0px;
  text-align: center;
}

.landingBack {
  height:600px;
  color: white;


  background: #16222a;
  background: -webkit-linear-gradient(to bottom, #16222a, #3a6073);
  background: -webkit-gradient(linear, left top, left bottom, from(#16222a), to(#3a6073));
  background: -webkit-linear-gradient(top, #16222a, #3a6073);
  background: -o-linear-gradient(top, #16222a, #3a6073);
  background: linear-gradient(to bottom, #16222a, #3a6073);
}

@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

.landingMain {
  font-family: 'Karla', sans-serif;
  letter-spacing: -10px;
