  color: white;
  line-height: 86px;
  text-align: left;
  -webkit-font-smoothing: antialiased !important;
}

.box {
  -ms-flex-line-pack: center;
      align-content: center;
}

.projectTitle {
  font-size: 76px;
  margin-bottom: 20px;
  padding-top: 12vh;
  color: #56BEFF;
	-webkit-animation: text-shadow-pop-br 0.6s both;
	        animation: text-shadow-pop-br 0.6s both;
  -webkit-font-smoothing: antialiased !important;
}

@-webkit-keyframes text-shadow-pop-br {
  0% {
    text-shadow: 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black;
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
  100% {
    text-shadow: 1px 1px black, 2px 2px black, 3px 3px black, 4px 4px black, 5px 5px black, 6px 6px black, 7px 7px black, 8px 8px black;
    -webkit-transform: translateX(-8px) translateY(-8px);
            transform: translateX(-8px) translateY(-8px);
  }
}
@keyframes text-shadow-pop-br {
  0% {
    text-shadow: 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black, 0 0 black;
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
  100% {
    text-shadow: 1px 1px black, 2px 2px black, 3px 3px black, 4px 4px black, 5px 5px black, 6px 6px black, 7px 7px black, 8px 8px black;
    -webkit-transform: translateX(-8px) translateY(-8px);
            transform: translateX(-8px) translateY(-8px);
  }
}

